.NavBar{
	/*background:white;*/
	position: fixed;
	height: 70px;
	width: 100vw;
	z-index: 100;
	padding: 0 50px;
  /*-moz-box-shadow:    1px 1px 3px 2px grey;*/
  /*-webkit-box-shadow: 1px 1px 3px 2px grey;*/
  /*box-shadow:         1px 1px 3px 2px grey;*/
}

.nav-cont{
	width: 100%;
	height: 100%;
	display: flex;
	align-items: flex-end;
	/*justify-content: space-between;*/
	color: black;
}
.nav-logo{
	height:50px;
	/*margin-bottom: 10px;*/
}
.nav-tit{
	margin-bottom: 15px;
	margin-left: 15px;
	font-size: 20px;
	font-weight: bold;
	font-family: 'QuickSand',sans-serif;
}
.nav-login-btn{
	border: 1px solid white;
	font-family: 'QuickSand',sans-serif;
	padding: 7px 20px;
	color: white;
	cursor: pointer;
	border-radius:3px;
	transition:0.5s;
	font-weight: bold;
}
.nav-login-btn:hover{
	color: black;
	background:white;
}
@media only screen and (max-width:10in){
	.NavBar{
		padding: 0 25px;
	}
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home{
	background: #000100;
	width:100vw;
	height: 100vh;
	/*overflow: hidden;*/
}
.land-main{
	/*background-position: center;*/
	/*background-size: cover;*/
	height:100vh;
	width:100vw;
	display: flex !important;
	align-items: center !important;
	justify-content: center;
	flex-direction: column;
	position: relative;
	/*padding-bottom: 15%;*/
	/*margin: 0 auto;*/
}
#landing-back{
	position: absolute;
	height: 100vh;
	z-index: 2;
	width: 100vw;
	/*background: rgba(0,0,0,0.1);*/
}
.land-main-banner{
	width:400px;
	height: 300px;
	/*margin-bottom:15%; */
}
.main-title{
	font-size:90px;
	color:black;
	z-index: 10;
	/*font-weight: bold;*/
	/*margin-bottom:2%;*/
	letter-spacing: 8px;
	font-family: 'Quicksand',sans-serif;
}
.main-tag-line{
	font-size: 25px;
	z-index: 10;
	color:black;
	margin-bottom:2%;
	font-family: 'Quicksand',sans-serif;
	word-spacing:150%;
	text-align: center;
}
.main-tag-line span{
	font-weight: bold;
	font-size: 27px;
}
.countdown-timer{
background-color: #FBDA61;
background-image: linear-gradient(45deg, #FBDA61 0%, #FF5ACD 100%);
	padding: 10px 15px;
	display: flex;
	flex-wrap: wrap;
	width:240px;
	margin-bottom: 15%;
	/*border-radius:5px;*/
}
.countdown-box{
	width:80px;
	font-size:15px; 
	text-align: center;
	color:black;
}
.countdown-font{
	font-size: 20px !important;
}

@media only screen and (max-width:10in){
.land-main{
	justify-content: center;
}
.land-main-banner{
	width:350px;
	height: 300px;
	margin-bottom:25%; 
}
.main-title{
	font-size:40px;
	margin-bottom:3%;
	letter-spacing:5px;
	/*margin-top: 20%;*/
	font-weight: bold;
}
.main-tag-line{
	font-size: 25px;
	margin-bottom: 4%;
}
.main-tag-line span{
	font-weight: bold;
	font-size: 18px;
}
.countdown-timer{
	margin-bottom:24vh;
}
}


/*css for scroll down*/

.App {
  width: 100vw;
  overflow-x: hidden;
  /*height: 100vh;*/
}
.divider{
	width: 80%;
	height: 1px;
	background: lightgrey;	
	margin: 0 auto;
}
.text-3{
	font-family:'QuickSand',sans-serif;
	font-size:14px;
	color:black;
}


